import React from "react";
import {Link} from "react-router-dom";

function Navbar(){
  return(
    <div>
<nav className="navbar navbar-expand navbar-dark bg-dark">
<ul className="navbar-nav ">
<Link to="/">
<li>
    <a className="active nav-link active" href="">Home</a>
</li>
</Link>
<Link to="/about">
    <li>
        <a className="nav-link " href="">About</a>
    </li>
    </Link>
    <Link to="/contact">
    <li>
        <a className="nav-link" href="">Contact</a>
    </li>
    </Link>
</ul>
<form className="form-section ml-auto">
<input placeholder="Search" type="text" className="mr-sm-2 form-control"></input>
<button type="button" className="btn btn-outline-info">Search</button>
</form>
</nav>
</div>
  );
}

export default Navbar;
