import React from "react";
import Headerbg from "../images/header-bg.jpg";

const headerBackground = {
  backgroundImage: `url(${Headerbg})`

}

function Header(){
  return(
<header style={headerBackground} id="header">
<div className="title">
<h1 className="heading">React Website</h1>
<p className="subheading">Lorem ipsum dolor sit amet consectetur  tempor incididunt ut labore.</p>
<a href="#" className="header-btn">Get in touch with the team today</a>
<div>
<a href="#card-deck"><i className="down-arrow fas fa-angle-double-down fa-3x"></i></a>
</div>
</div>

</header>

  );
}

export default Header;
